	outline:none;
	text-transform:uppercase;
}

.btn-primary:hover,
.btn-primary:focus {
	background-color:#333;
	color:#fff;
	outline:none;
	border:0;
}

.parallax-section {
	background-position: 50% 0;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment:fixed;
	width: 100%;
	min-height:437px;
	color:#fff;
	position:relative;
}

.overlay-bg {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
}

